.main{
   -webkit-transition: 0.5s;
   -o-transition: 0.5s;
   transition: 0.5s;
   display:-ms-flexbox;
   display:flex;
}
.reminderCont{
    
   -webkit-box-sizing: border-box;
    
           box-sizing: border-box;
   border: 1px solid rgb(22, 0, 75,0.5);
    border-radius:15px;
    margin: 0 20px;
   
    width: 15rem;
    opacity:0;
    -webkit-transition: 0.5s ease-in-out;
    -o-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out
   
}
.open {
    display:block;
    
}
.open2 {
    opacity:1;
}
.calendar {
font-size:40px;
font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;


}
.calendarCont{
    display:grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-auto-flow: row;
    border: 1px solid rgb(22, 0, 75,0.5);
    border-bottom-left-radius:15px;
    border-bottom-right-radius:15px;
}
.CalendarItemHeader{
    
    background-color: #969696
}
.CalendarItem {

    border-radius: 3px;


}
.CalendarItem:hover{
    background-color: rgb(201, 200, 200);
}
.CalendarItemSide{
    color: rgb(170, 164, 164);

    border-radius: 3px;
}
.CalendarItemCurr {
    color:red;

}
.CalendarItemCurr:hover {
    background-color: rgb(201, 200, 200);
}
.deleteButton {
    background:none;
    color: red;
}


