.weekdays{
    display: -ms-flexbox;
    display: flex;

    list-style:none;
    margin:0;
    padding:0;
    background-color: rgba(0, 23, 231, 0.5);
    color: #01262c;
    border: 1px solid rgba(23, 41, 204, 0.5);
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    font-size:1.9rem;
    
}
.weekday{
    -ms-flex:  2 1;
        flex:  2 1;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align:center;
        align-items:center;
    border-radius: 5px;
    padding:20px 0;
}
.weekday1{
    -ms-flex:  1 1;
        flex:  1 1;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align:center;
        align-items:center;
    border-radius: 5px;
    padding:20px 0;
}
.right{
    width: 0;
    height: 0;
    border: 0 solid transparent;
    border-bottom-width: 15px;
    border-top-width: 15px;
    border-right: 30px solid rgb(255, 255, 255);
    background: none;

}
.left{

    width: 0;
    height: 0;
    border: 0 solid transparent;
    border-bottom-width: 15px;
    border-top-width: 15px;
    border-left: 30px solid rgb(255, 255, 255);
    background: none;
}




