h3 {
    padding: 10px;
}
#rem{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
#addButton{
    border: none;
    color: rgba(7, 0, 0, 0.63);
    padding: 16px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 0 0 0;
    -webkit-transition-duration: 0.4s; /* Safari */
    -o-transition-duration: 0.4s;
       transition-duration: 0.4s;
    cursor: pointer;
    border-radius:14px;
 
}
#addButton:hover {

    background-color: rgba(0, 23, 231, 0.5);
    color: white;
}
.reminderList{
    -ms-flex:1 1;
        flex:1 1;
    padding:0;
    overflow:auto;
    word-wrap: break-word;
}
.reminderList > li{
text-align: left;
margin-left: 20px;
margin-right: 10px;
border-bottom: 1px solid black;
}
li > button {
    background:none;
    border:none;
    color: red;
}


